body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: -webkit-flex;
  display: flex;
  position: relative;
  background-color: black;
  color: white;
}

.spirograph{
  width: 500px;
}

.draw-container {
  position: relative;
}

.container-disc {
  position: absolute;
  top: 0;
  left: 0;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  border: 1px solid #fffa;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.disc {
  border-radius: 50%;
  background-color: brown;
}

.control-panel {
  padding: 20px;
  width: 30%;
}

.control-panel > * {
  margin-bottom: 20px;
}

.spirograph-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 70%;
}

.samplesSelectRange .rc-slider-mark {
  display: none;
}

.color {
  text-align: center;
  font-weight: 700;
  border-radius: 6px;
  margin-bottom: 5px;
  min-width: 50px;
  min-height: 10px;
}

.points-info {
  text-align: center;
}

